.select {
    border-radius: 5px;
    height: 42px;
    outline: none;
    background: white;
    font-family: europa;
    font-weight: 200;
    font-size: 14px;
    color: #445870;
}

.react-select__placeholder {
    color: #445870;
}

.react-select__control {
    border: 1px solid #E0E7EE;

    &:hover {
        cursor: pointer;
    }
}